@import url("https://fonts.googleapis.com/css2?family=Inter:wght@100;200;300;400;500;600;700;800;900&display=swap");

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body {
    background-color: rgb(248, 246, 253);
}

body,
input,
textarea,
button {
    font-family: "Inter", sans-serif;

}

/* .app {
  width: 400px;
  margin: 0 auto;
  height: 100vh;
} */

/* Contact.js */
.form {
    height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    /* background-color: pink; */
    background: rgb(131, 58, 180);
    background: linear-gradient(90deg, rgba(131, 58, 180, 1) 0%, rgba(253, 29, 29, 1) 0%, rgba(252, 176, 69, 1) 39%);

}

.form>h1 {
    margin-bottom: 30px;
}

.form>input,
textarea,
select {
    padding: 20px;
    border-radius: 3px;
    /* box-shadow: 0px 0px 5px rgba(0, 0, 0, 0.137); */
    margin-bottom: 20px;
    border: 1px solid lightgray;
    /* border: none; */
    background: #fff;
    font-size: 16px;
    color: rgb(0, 0, 32);
    outline: none;
    width: 500px;
    border-radius: 10px;

}

.form>input:focus,
textarea:focus {
    border: 1px solid rgb(239, 21, 217);
}

.form>textarea {
    height: 150px;
    max-width: 500px;
    min-height: 100px;
}

.form>label {
    padding-bottom: 10px;
    color: rgb(0, 0, 32);
    font-weight: bold;
}


.form>button {
    padding: 20px;
    border: none;
    background-color: rgb(229, 56, 214);
    font-weight: 500;
    font-size: 20px;
    border-radius: 10px;
    color: #fff;
    cursor: pointer;
    transition: 0.2s ease-in-out;
    margin-top: 10px;
    width: 500px;
}

.form>button:hover {
    background-color: rgb(235, 24, 238);
}

.check-box {
    margin-bottom: 20px;
}


@media only screen and (max-width: 600px) {
    .form>h1 {
        margin-bottom: 20px;
        margin-top: 50px;
    }

    .form>input,
    textarea,
    select {
        padding: 20px;
        border-radius: 3px;
        /* box-shadow: 0px 0px 5px rgba(0, 0, 0, 0.137); */
        margin-bottom: 20px;
        border: 1px solid lightgray;
        /* border: none; */
        background: #fff;
        font-size: 16px;
        color: rgb(0, 0, 32);
        outline: none;
        width: 300px;
        border-radius: 10px;
    }

    .form>button {
        padding: 20px;
        border: none;
        background-color: rgb(229, 56, 214);
        font-weight: 500;
        font-size: 20px;
        border-radius: 3px;
        color: #fff;
        cursor: pointer;
        transition: 0.2s ease-in-out;
        margin-top: 10px;
        margin-bottom: 80px;
        width: 300px;
        border-radius: 10px;
    }
}