@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600;700&display=swap');

body {
	line-height: 3;
	font-family: 'Poppins', sans-serif;
}

* {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
}

.container {
	max-width: 1170px;
	margin: auto;
}

.row {
	display: flex;
	flex-wrap: wrap;
}

ul {
	list-style: none;
}

.footer {
	background-image: url(../../assets/img/footer-1.png);
	background-size: cover;
	background-repeat: no-repeat;
	background-position: center center;
	background-color: #f2cd13;
	padding: 90px 0;

}

.footer-col {
	width: 20%;
	padding: 5px 10px;
}

.footer-col h4 {
	font-size: 18px;
	color: #ffff;
	text-transform: capitalize;
	margin-bottom: 15px;
	margin-top: -80px;
	font-weight: 900;
	position: relative;
}

.footer-col h4::before {
	content: '';
	position: absolute;
	left: 0;
	bottom: 5px;
	background-color: #f266b3;
	height: 3px;
	box-sizing: border-box;
	width: 50px;


}

.footer-col ul li:not(:last-child) {
	margin-bottom: 5px;
}

.footer-col ul li a {
	font-size: 16px;
	text-transform: capitalize;
	color: #0d0d0d;
	text-decoration: none;
	font-weight: 300;
	color: #0d0d0d;
	display: block;
	transition: all 0.3s ease;
	font-weight: 600;
    margin-bottom: -15px;

}

.footer-col ul li a:hover {
	color: #f266b3;
	padding-left: 8px;

}

