/*responsive*/
@media(max-width: 767px) {
	.footer-col {
		width: 50%;
		margin-bottom: 30px;
	}

	.footer {
		background-image: url(../../assets/img/Globe-2.png);
		background-size: cover;
		background-repeat: no-repeat;
		background-position: center center;
		background-color: #0d0d0d;
		padding: 70px 0;

	}
	.footer-col h4 {
		font-size: 20px;
		color: #f266b3;
		text-transform: capitalize;
		margin-bottom: 10px;
		margin-top: 5px;
		font-weight: 700;
		position: relative;
	}
	.footer-col ul li a {
		font-size: 16px;
		text-transform: capitalize;
		color: #0d0d0d;
		text-decoration: none;
		font-weight: 300;
		color: #ffff;
		display: block;
		transition: all 0.3s ease;
		font-weight: 600;
		margin-bottom: -15px;

	}
}

@media(max-width: 574px) {
	.footer-col {
		width: 100%;
	}
}